<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 家族编码 -->
            <a-form-model-item
              label="家族编码"
              prop="familyCode"
              ref="familyCode"
            >
              <a-input
                v-model="entity.familyCode"
                placeholder="请输入家族编码，格式为F开头加4位数字"
                name="familyCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 家族名称 -->
            <a-form-model-item
              label="家族名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入家族名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 产品分类 -->
            <a-form-model-item
              label="产品分类"
              prop="category"
              ref="category"
            >
              <a-select
                v-model="entity.category"
                placeholder="请选择产品分类"
                name="category"
              >
                <a-select-option value="电子设备">电子设备</a-select-option>
                <a-select-option value="智能家居">智能家居</a-select-option>
                <a-select-option value="网络设备">网络设备</a-select-option>
                <a-select-option value="办公设备">办公设备</a-select-option>
                <a-select-option value="安防设备">安防设备</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 制造商 -->
            <a-form-model-item
              label="制造商"
              prop="manufacturer"
              ref="manufacturer"
            >
              <a-select
                v-model="entity.manufacturer"
                placeholder="请选择制造商"
                name="manufacturer"
              >
                <a-select-option value="华为">华为</a-select-option>
                <a-select-option value="小米">小米</a-select-option>
                <a-select-option value="联想">联想</a-select-option>
                <a-select-option value="戴尔">戴尔</a-select-option>
                <a-select-option value="惠普">惠普</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status" name="status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="18">
            <!-- 家族描述 -->
            <a-form-model-item 
              label="家族描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入家族描述"
                :rows="4"
                :max-length="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 产品标识特征 -->
      <g-collapse headerName="产品标识特征">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-feature-btn" type="primary" icon="plus" @click="addFeature">添加特征</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="featureColumns" 
                :dataSource="entity.features" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="featureName" slot-scope="text, record, index">
                  <a-input v-model="entity.features[index].featureName" placeholder="请输入特征名称" />
                </template>
                <template slot="featureDescription" slot-scope="text, record, index">
                  <a-input v-model="entity.features[index].featureDescription" placeholder="请输入特征描述" />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button type="danger" icon="delete" size="small" @click="() => removeFeature(index)"></a-button>
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditProductFamily",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        familyCode: "",
        name: "",
        category: "",
        manufacturer: "",
        description: "",
        status: "启用",
        features: [],
        productCount: 0,
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      rules: {
        familyCode: [
          { required: true, message: "请输入家族编码", trigger: "change" },
          { pattern: /^F\d{4}$/, message: "家族编码必须为F开头加4位数字", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入家族名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "家族名称不能包含前后空格", trigger: "change" }
        ],
        category: [
          { required: true, message: "请选择产品分类", trigger: "change" }
        ],
        manufacturer: [
          { required: true, message: "请选择制造商", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ]
      },
      featureColumns: [
        {
          title: "特征名称",
          dataIndex: "featureName",
          key: "featureName",
          width: "40%",
          scopedSlots: { customRender: "featureName" }
        },
        {
          title: "特征描述",
          dataIndex: "featureDescription",
          key: "featureDescription",
          width: "40%",
          scopedSlots: { customRender: "featureDescription" }
        },
        {
          title: "操作",
          key: "action",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取产品家族数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        familyCode: "",
        name: "",
        category: "",
        manufacturer: "",
        description: "",
        status: "启用",
        features: [],
        productCount: 0,
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        this.entity = {
          ...this.entity,
          ...row,
          // 确保features数组被正确初始化
          features: row.features || []
        };
      }
    },
    
    async save() {
      // 保存 校验
      let res = await this.checkError();
      if (res) {
        // 检查特征表，确保每个特征都有名称和描述
        const invalidFeature = this.entity.features.find(
          feature => !feature.featureName || !feature.featureDescription
        );
        
        if (invalidFeature) {
          this.$message.error("所有特征必须有名称和描述");
          return;
        }

        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    addFeature() {
      this.entity.features.push({
        id: Date.now(), // 使用时间戳作为临时ID
        featureName: "",
        featureDescription: ""
      });
    },
    
    removeFeature(index) {
      this.entity.features.splice(index, 1);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-feature-btn {
  margin-bottom: 16px;
}
</style> 